body{
  background-image: url('../assets/mas-seal-timeline.jpg');
  /* background-size: cover; */
  background-position: center;
  background-position: center -100%;
  /* overflow: hidden; */
  height: 80vh; 
}
.container {
  max-width: 90%; /* Changed to 90% of the viewport width */
  margin: 0 auto; /* Center the container */
  padding: 20px;
  text-align: center; /* Center align text */
}
.example-questions{
  z-index: 5;
  backdrop-filter: blur(10px);
  width: 15%;
}

.plans-modal{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  /* z-index: 1000; */
  border: 2px solid #141111;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.input-field {
  width: calc(100% - 40px); /* Adjusted width */
  padding: 10px;
  margin-bottom: 10px;
  font-size: 16px;
}
.message.user {
  margin: 8px 0;
  background-color: #e7cf80;
  align-self: flex-end; /* Aligns user messages to the right */
}

.submit-button {
  background-color: #e7cf80;
  color: #fff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
}

.summary-container,
.sources-container {
  margin-top: 20px;
}



ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin-bottom: 5px;
}
